export default function DailyChallengesScreen() {
  const { state } = useAppContext();
  const { completeChallenge, setError } = useAppActions();
  
  const [activeTab, setActiveTab] = useState('today');
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null);
  const [expandedChallenge, setExpandedChallenge] = useState<string | null>(null);
  const [completingChallenge, setCompletingChallenge] = useState<string | null>(null);

  // Filtra desafios baseado na aba ativa
  const getFilteredChallenges = () => {
    if (activeTab === 'today') {
      return state.dailyChallenges;
    }
    
    let challenges = allChallenges;
    if (selectedCategory) {
      challenges = challenges.filter(c => c.category === selectedCategory);
    }
    
    return challenges.map(challenge => ({
      ...challenge,
      completed: state.userProgress.completedChallenges.includes(challenge.id)
    }));
  };

  const filteredChallenges = getFilteredChallenges();
  const completedToday = state.dailyChallenges.filter(c => c.completed).length;
  const totalToday = state.dailyChallenges.length;
  const progressPercentage = totalToday > 0 ? (completedToday / totalToday) * 100 : 0;

  const handleCompleteChallenge = async (challengeId: string) => {
    setCompletingChallenge(challengeId);
    setError(null);

    try {
      // Simula delay de processamento
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      completeChallenge(challengeId);
      
      // Feedback visual
      setTimeout(() => {
        setCompletingChallenge(null);
      }, 500);

    } catch (error) {
      console.error('Erro ao completar desafio:', error);
      setError('Não foi possível completar o desafio. Tente novamente.');
      setCompletingChallenge(null);
    }
  };

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'easy': return 'bg-green-100 text-green-800';
      case 'medium': return 'bg-yellow-100 text-yellow-800';
      case 'hard': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getDifficultyText = (difficulty: string) => {
    switch (difficulty) {
      case 'easy': return 'Fácil';
      case 'medium': return 'Médio';
      case 'hard': return 'Difícil';
      default: return 'Normal';
    }
  };

  const tabs = [
    { id: 'today', label: 'Hoje', count: totalToday },
    { id: 'all', label: 'Todos', count: allChallenges.length }
  ];

  return (
    <div className="min-h-screen bg-gradient-soft">
      <TopNavigation 
        title="Desafios Diários" 
        showBack={true}
        onBack={() => window.history.back()}
      />
      
      <div className="container-mobile py-6 pb-24 space-y-6">
        {/* Progress Card */}
        {activeTab === 'today' && (
          <Card className="card-elevated bg-gradient-calm text-white">
            <CardContent className="pt-6">
              <div className="text-center space-y-4">
                <h2 className="text-xl font-bold">Progresso de Hoje</h2>
                <div className="text-3xl font-bold">
                  {completedToday}/{totalToday}
                </div>
                <Progress 
                  value={progressPercentage} 
                  className="h-3 bg-white/20"
                />
                <p className="text-sm opacity-90">
                  {completedToday === totalToday 
                    ? '🎉 Parabéns! Você completou todos os desafios de hoje!'
                    : `Faltam ${totalToday - completedToday} desafios para completar o dia`
                  }
                </p>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Error Alert */}
        {state.error && (
          <Alert className="border-red-200 bg-red-50">
            <AlertDescription className="text-red-800">
              {state.error}
            </AlertDescription>
          </Alert>
        )}

        {/* Tabs */}
        <TabNavigation 
          tabs={tabs}
          activeTab={activeTab}
          onTabChange={setActiveTab}
        />

        {/* Category Filter (only for 'all' tab) */}
        {activeTab === 'all' && (
          <div className="space-y-3">
            <h3 className="font-semibold text-gray-900">Categorias</h3>
            <div className="flex flex-wrap gap-2">
              <Button
                variant={selectedCategory === null ? "default" : "outline"}
                size="sm"
                onClick={() => setSelectedCategory(null)}
                className="text-xs"
              >
                Todas
              </Button>
              {challengeCategories.map((category) => (
                <Button
                  key={category.id}
                  variant={selectedCategory === category.id ? "default" : "outline"}
                  size="sm"
                  onClick={() => setSelectedCategory(category.id)}
                  className="text-xs"
                >
                  {category.name}
                </Button>
              ))}
            </div>
          </div>
        )}

        {/* Challenges List */}
        <div className="space-y-4">
          {filteredChallenges.map((challenge) => {
            const isExpanded = expandedChallenge === challenge.id;
            const isCompleting = completingChallenge === challenge.id;
            const categoryInfo = challengeCategories.find(c => c.id === challenge.category);

            return (
              <Card 
                key={challenge.id}
                className={cn(
                  "card-soft transition-all duration-200",
                  challenge.completed ? "bg-green-50 border-green-200" : "hover:shadow-md",
                  isExpanded ? "ring-2 ring-blue-200" : ""
                )}
              >
                <CardContent className="pt-4">
                  <div className="space-y-3">
                    {/* Header */}
                    <div className="flex items-start justify-between">
                      <div className="flex-1">
                        <div className="flex items-center space-x-2 mb-2">
                          {categoryInfo && (
                            <Badge className={cn("text-xs", categoryInfo.color)}>
                              {categoryInfo.name}
                            </Badge>
                          )}
                          {'difficulty' in challenge && (
                            <Badge className={cn("text-xs", getDifficultyColor(challenge.difficulty))}>
                              {getDifficultyText(challenge.difficulty)}
                            </Badge>
                          )}
                          <Badge variant="outline" className="text-xs">
                            +{challenge.points} pts
                          </Badge>
                        </div>
                        
                        <h3 className="font-semibold text-gray-900 mb-1">
                          {challenge.title}
                        </h3>
                        <p className="text-sm text-gray-600">
                          {challenge.description}
                        </p>
                        
                        {'duration' in challenge && (
                          <div className="flex items-center space-x-4 mt-2 text-xs text-gray-500">
                            <span>⏱️ {challenge.duration} min</span>
                          </div>
                        )}
                      </div>
                      
                      <div className="flex items-center space-x-2 ml-4">
                        {challenge.completed ? (
                          <div className="w-8 h-8 bg-green-500 rounded-full flex items-center justify-center">
                            <div className="w-4 h-4 border-2 border-white rounded-full" />
                          </div>
                        ) : (
                          <Button
                            size="sm"
                            onClick={() => handleCompleteChallenge(challenge.id)}
                            disabled={isCompleting}
                            className="btn-primary text-xs"
                          >
                            {isCompleting ? '...' : 'Completar'}
                          </Button>
                        )}
                      </div>
                    </div>

                    {/* Instructions (expandable) */}
                    {'instructions' in challenge && (
                      <div className="space-y-2">
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => setExpandedChallenge(
                            isExpanded ? null : challenge.id
                          )}
                          className="text-xs text-blue-600 p-0 h-auto"
                        >
                          {isExpanded ? 'Ocultar instruções' : 'Ver instruções'}
                        </Button>
                        
                        {isExpanded && (
                          <div className="bg-blue-50 p-3 rounded-lg space-y-2">
                            <h4 className="font-medium text-blue-900 text-sm">
                              Como fazer:
                            </h4>
                            <ol className="text-sm text-blue-800 space-y-1">
                              {challenge.instructions.map((instruction, index) => (
                                <li key={index} className="flex items-start">
                                  <span className="font-medium mr-2 text-blue-600">
                                    {index + 1}.
                                  </span>
                                  <span>{instruction}</span>
                                </li>
                              ))}
                            </ol>
                          </div>
                        )}
                      </div>
                    )}
                  </div>
                </CardContent>
              </Card>
            );
          })}
        </div>

        {/* Empty State */}
        {filteredChallenges.length === 0 && (
          <div className="text-center py-12 space-y-4">
            <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto">
              <div className="w-8 h-8 bg-gray-400 rounded-full" />
            </div>
            <h3 className="text-lg font-semibold text-gray-900">
              Nenhum desafio encontrado
            </h3>
            <p className="text-gray-600">
              {selectedCategory 
                ? 'Tente selecionar uma categoria diferente'
                : 'Novos desafios serão adicionados em breve'
              }
            </p>
          </div>
        )}

        {/* Stats Summary */}
        {activeTab === 'today' && completedToday > 0 && (
          <Card className="card-soft">
            <CardContent className="pt-4">
              <div className="text-center space-y-2">
                <h3 className="font-semibold text-gray-900">
                  Pontos Ganhos Hoje
                </h3>
                <div className="text-2xl font-bold text-green-600">
                  +{state.dailyChallenges
                    .filter(c => c.completed)
                    .reduce((sum, c) => sum + c.points, 0)} pontos
                </div>
                <p className="text-sm text-gray-600">
                  Continue assim para manter sua sequência!
                </p>
              </div>
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  );
}
